import React from "react";
import { Link } from "react-router-dom";
import {
  Panel,
  PanelHeader,
  PanelBody,
} from "./../../components/panel/panel.jsx";
import { Line } from "react-chartjs-2";
import { getData } from "../../config/axios.jsx";
import { getItem, setItem } from "../../components/helpers/function.jsx";
import PerfectScrollbar from 'react-perfect-scrollbar'
import 'react-perfect-scrollbar/dist/css/styles.css';
class DashboardV1 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      penjualan_hari_ini: 0,
      //   penjualan_bulanan: [0,3,2,1],

      pembelian_hari_ini: 0,
      pembelian_bulanan: 0,

      pesanan_hari_ini: 0,
      pesanan_bulanan: 0,

      service_hari_ini: 0,
      service_bulanan: 0,
    };

    this.lineChartData = {
      labels: [
        "JANUARI",
        "FEBRUARI",
        "MARET",
        "APRIL",
        "MEI",
        "JUNI",
        "JULI",
        "AGUSTUS",
        "SEPTEMBER",
        "OKTOBER",
        "NOVEMBER",
        "DESEMBER",
      ],
      datasets: [
        {
          label: "Penjualan",
          fill: false,
          lineTension: 0.1,
          backgroundColor: "rgba(0, 172, 172, 0.25)",
          borderColor: "#FF5B57",
          borderWidth: 2,
          pointBorderColor: "#FF5B57",
          pointBackgroundColor: "#fff",
          pointBorderWidth: 2,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: "#fff",
          pointHoverBorderColor: "#FF5B57",
          pointHoverBorderWidth: 3,
          pointRadius: 3,
          pointHitRadius: 10,
          data: getItem("data_penjualan_bulanan"),
        },
        {
          label: "Pembelian",
          fill: false,
          lineTension: 0.1,
          backgroundColor: "rgba(52, 143, 226, 0.25)",
          borderColor: "#F59C1A",
          borderWidth: 2,
          pointBorderColor: "#F59C1A",
          pointBackgroundColor: "#fff",
          pointBorderWidth: 2,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: "#fff",
          pointHoverBorderColor: "#F59C1A",
          pointHoverBorderWidth: 3,
          pointRadius: 3,
          pointHitRadius: 10,
          data: getItem("data_pembelian_bulanan"),
        },
      ],
    };

    this.lineChartOptions = {
      maintainAspectRatio: false,
      scales: {
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
              fontColor: "black",
            },
          },
        ],
        xAxes: [
          {
            ticks: {
              fontColor: "black",
            },
          },
        ],
      },
      legend: {
        labels: {
          fontColor: "black",
        },
      },
    };
  }

  componentDidMount() {
    getData("penjualan/chart?type=today")
      .then((res) => {
        this.setState({
          penjualan_hari_ini: res.data.value || 0,
        });
      })
      .catch((err) => {
        console.log(err);
      });
    getData("penjualan/chart?type=monthly")
      .then((res) => {
        setItem("data_penjualan_bulanan", res.data.value);
      })
      .catch((err) => {
        console.log(err);
      });

    getData("pembelian/chart?type=monthly")
      .then((res) => {
        setItem("data_pembelian_bulanan", res.data.value);
      })
      .catch((err) => {
        console.log(err);
      });
    getData("pembelian/chart?type=today")
      .then((res) => {
        this.setState({
          pembelian_hari_ini: res.data.value || 0,
        });
      })
      .catch((err) => {
        console.log(err);
      });
    getData("pesanan/chart?type=today")
      .then((res) => {
        this.setState({
          pesanan_hari_ini: res.data.value || 0,
        });
      })
      .catch((err) => {
        console.log(err);
      });
    getData("service/chart?type=today")
      .then((res) => {
        this.setState({
          service_hari_ini: res.data.value || 0,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Home</Link>
          </li>
          <li className="breadcrumb-item active">Dashboard</li>
        </ol>
        <h1 className="page-header">Dashboard</h1>

        <div className="row">
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-red">
              <div className="stats-icon">
                <i className="fa fa-shopping-cart"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL PENJUALAN HARI INI</h4>
                <p>{this.state.penjualan_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-penjualan">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-orange">
              <div className="stats-icon">
                <i className="fa fa-shopping-bag"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL PEMBELIAN HARI INI</h4>
                <p>{this.state.pembelian_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-pembelian">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-grey-darker">
              <div className="stats-icon">
                <i className="fa fa-boxes"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL PESANAN HARI INI</h4>
                <p>{this.state.pesanan_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-pesanan">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-black-lighter">
              <div className="stats-icon">
                <i className="fa fa-tools"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL SERVICE HARI INI</h4>
                <p>{this.state.service_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-services">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-xl-9">
            <Panel>
              <PanelHeader>Analisa Transaksi / Bulan</PanelHeader>
              <PanelBody>
                <Line
                  data={this.lineChartData}
                  height={300}
                  options={this.lineChartOptions}
                />
              </PanelBody>
            </Panel>
          </div>
          <div className="col-xl-3">
            <Panel>
              <PanelHeader>Release Note (18-08-2021)</PanelHeader>
              <PanelBody>
                <div className="text-success" style={{height: "300px", overflowY: "auto"}}>
                <PerfectScrollbar>
                  <ul>
                    <li>
                      Master Data Group Penambhan Jenis Group ( TUA, MUDA )
                    </li>
                    <li>Penambahan Kadar Di Transaksi Pembelian </li>
                    <li>Penambahan FIltering Jenis Group Di Laporan Transaksi Penjualan </li>
                    <li>Penambahan FIltering Jenis Group Di Laporan Transaksi Penjualan Sales </li>
                    <li>Penambahan FIltering Jenis Group Di Laporan Transaksi Penjualan Market Place </li>
                    <li>Penambahan FIltering Jenis Group Di Laporan Transaksi Pembelian </li>
                    <li>Penambahan FIltering Jenis Group Di Laporan Transaksi History Pembelian </li>
                    <li>Penambahan FIltering Jenis Group Di Laporan Transaksi Pembelian Sales </li>
                    <li>Penambahan Laporan Penjualan Margin </li>
                    <li>Penambahan Limit Jual Tukar </li>
                    <li>Peningkatan Performa</li>
                  </ul>
                </PerfectScrollbar>
                </div>

              </PanelBody>
            </Panel>
          </div>
        </div>
      </div>
    );
  }
}

export default DashboardV1;
